const BREVO_FORM_STYLESHEET =
  "https://sibforms.com/forms/end-form/build/sib-styles.css";
const BREVO_FORM_SCRIPT = "https://sibforms.com/forms/end-form/build/main.js";
const RECAPTCHA_SCRIPT = "https://www.google.com/recaptcha/api.js?hl=en";

class SignUp extends Component {
  componentDidMount() {
    this.injectBrevoAssets();
  }

  componentWillUnmount() {
    this.removeBrevoAssets();
  }

  injectBrevoAssets() {
    if (typeof document === "undefined") {
      return;
    }

    if (!document.querySelector('link[data-brevo-form="stylesheet"]')) {
      this.brevoStylesheetEl = document.createElement("link");
      this.brevoStylesheetEl.rel = "stylesheet";
      this.brevoStylesheetEl.href = BREVO_FORM_STYLESHEET;
      this.brevoStylesheetEl.setAttribute("data-brevo-form", "stylesheet");
      document.head.appendChild(this.brevoStylesheetEl);
    }

    if (!document.querySelector('style[data-brevo-form="inline"]')) {
      this.brevoInlineStyleEl = document.createElement("style");
      this.brevoInlineStyleEl.setAttribute("data-brevo-form", "inline");
      this.brevoInlineStyleEl.type = "text/css";
      this.brevoInlineStyleEl.appendChild(
        document.createTextNode(BREVO_STYLE_BLOCK)
      );
      document.head.appendChild(this.brevoInlineStyleEl);
    }

    if (!document.querySelector('script[data-brevo-form="config"]')) {
      this.brevoConfigScript = document.createElement("script");
      this.brevoConfigScript.type = "text/javascript";
      this.brevoConfigScript.setAttribute("data-brevo-form", "config");
      this.brevoConfigScript.appendChild(
        document.createTextNode(`
          window.REQUIRED_CODE_ERROR_MESSAGE = 'Please choose a country code';
          window.LOCALE = 'en';
          window.EMAIL_INVALID_MESSAGE = window.SMS_INVALID_MESSAGE = "The information provided is invalid. Please review the field format and try again.";
          window.REQUIRED_ERROR_MESSAGE = "This field cannot be left blank. ";
          window.GENERIC_INVALID_MESSAGE = "The information provided is invalid. Please review the field format and try again.";
          window.translation = {
            common: {
              selectedList: '{quantity} list selected',
              selectedLists: '{quantity} lists selected',
              selectedOption: '{quantity} selected',
              selectedOptions: '{quantity} selected',
            }
          };
          window.handleCaptchaResponse = function handleCaptchaResponse() {
            var captcha = document.getElementById('sib-captcha');
            if (!captcha) {
              return;
            }
            var event = new Event('captchaChange');
            captcha.dispatchEvent(event);
          };
          var AUTOHIDE = Boolean(0);
        `)
      );
      document.body.appendChild(this.brevoConfigScript);
    }

    if (!document.querySelector('script[data-brevo-form="main"]')) {
      this.brevoMainScript = document.createElement("script");
      this.brevoMainScript.src = BREVO_FORM_SCRIPT;
      this.brevoMainScript.defer = true;
      this.brevoMainScript.setAttribute("data-brevo-form", "main");
      document.body.appendChild(this.brevoMainScript);
    }

    if (!document.querySelector('script[data-brevo-form="recaptcha"]')) {
      this.recaptchaScript = document.createElement("script");
      this.recaptchaScript.src = RECAPTCHA_SCRIPT;
      this.recaptchaScript.async = true;
      this.recaptchaScript.defer = true;
      this.recaptchaScript.setAttribute("data-brevo-form", "recaptcha");
      document.body.appendChild(this.recaptchaScript);
    }
  }

  removeBrevoAssets() {
    const elements = [
      this.brevoStylesheetEl,
      this.brevoInlineStyleEl,
      this.brevoConfigScript,
      this.brevoMainScript,
      this.recaptchaScript,
    ];
    elements.forEach((node) => {
      if (node && node.parentNode) {
        node.parentNode.removeChild(node);
      }
    });
  }

  render() {
    const theme = this.props.theme;

    return (
      <div>
        <Header theme={theme} />
        <main className="signup-main">
          <Fade bottom duration={2000} distance="20px" delay={200}>
            <div
              className="brevo-form-wrapper"
              dangerouslySetInnerHTML={{ __html: BREVO_FORM_HTML }}
            />
          </Fade>
        </main>
        <TopButton theme={theme} />
      </div>
    );
  }
}

export default SignUp;
